export async function loginUser(data: { pen_number: string; password: string }) {
  return apiPost('/api/auth/login', data)
}

export async function registerUser(data: {
  first_name: string
  surname: string
  other_names?: string
  email: string
  pen_number: string
  password: string
}) {
  return apiPost('/api/auth/register', data)
}
